import React, { Component } from 'react';
import articleContent from './article-content';
import CommentsList from '../components/CommentsList';
import Upvote from '../components/Upvote';
import AddCommentForm from '../components/AddCommentForm'

class ArticlePage extends Component {

  state = {
    articleInfo: {}
  }

  getArticles = () => {
      return articleContent.find(article => article.name === this.props.match.params.name)
  }

  upvoteArticle = async () => {
    const result = await fetch(`/api/articles/${this.props.match.params.name}/upvote`, {
      method:'POST'
    });
    const body = await result.json();
    this.setState({
      articleInfo: body
    });
  }

  addComment = async(username, text) => {
    const result = await fetch(`/api/articles/${this.props.match.params.name}/add-comment`, {
      method:'POST',
      body: JSON.stringify({username: username, text: text}),
      headers: {
        'Content-Type': 'application/json'
      }
    });
    const body = await result.json();
    this.setState({
      articleInfo: body
    });
  }

  componentDidMount() {
    const fetchData = async() => {
      const result = await fetch(`/api/articles/${this.props.match.params.name}`);
      console.log(result);
      const body = await result.json();
      console.log(body);
      this.setState({
        articleInfo: body
      });
    }
    fetchData();


  }
  
  render() {
    return (
      <div>
        {
          !this.getArticles() ? 
            <h1>No articles found</h1>
           : (
             
              <>
              <h1>{this.getArticles().title}</h1>
              <Upvote onClick={this.upvoteArticle}/>
              <p>This post has been upvoted : {this.state.articleInfo.upvotes} times</p>
              {this.getArticles().content.map((paragraph, key) => <p key={key}>{paragraph}</p>)}
              <CommentsList comments={this.state.articleInfo.comments}/>
              <AddCommentForm newComment={this.addComment}/>
              </>
           )
        }
      </div>
    );
  }
}

export default ArticlePage;