import React, { Component } from 'react';
import {Link} from 'react-router-dom';

class ArticleList extends Component {
  render() {
    return (
      <>
      <h3> <Link to={`/article/${this.props.article.name}`}> {this.props.article.title}</Link>  </h3>
      <p>{this.props.article.content[0].substring(0, 150)}
        <Link to={`/article/${this.props.article.name}`}> More...</Link>
      </p>
      </>
    );
  }
}

export default ArticleList;