import React, { Component } from 'react';

export default class AddCommentForm extends Component {

  state = {
    username: "",
    text: ""
  }

  usernameChagned = (event) => {
    console.log(event.target.value);
      this.setState({
        username: event.target.value
      })
  }

  commentChanged = (event) => {
    this.setState({
      text: event.target.value
    })
  }

  buttonTapped = () => {
    this.props.newComment(this.state.username, this.state.text);
    this.setState({
      username: ""
    })
    this.setState({
      text: ""
    })
  }

  render() {
    return (
      <div id="add-comment-form">
        <h3>Add a comment </h3>
        <label>Name:</label>
        <input type="text" value={this.state.username} onChange={this.usernameChagned}/>
        <label>Comment:</label>
        <textarea rows="4" cols="50" value={this.state.text} onChange={this.commentChanged}/>
        <button onClick={this.buttonTapped} > Add Comment</button>
      </div>
    );
  }
}
